export default function (script, HEAD, Async) {
  return new Promise(function (resolve, reject) {
    if (!script || !script.src) reject(new Error('missing src'))
    const head = HEAD || document.head || document.getElementsByTagName('head')[0]
    const ID = head.querySelector(`#${script.id}`)
    if (ID) {
      resolve(script.id)
    }
    const el = document.createElement('script')
    el.type = 'text/javascript'
    el.id = script.id
    el.async = Async
    el.onerror = function (e) {
      reject(e)
    }
    el.onload = function (e) {
      resolve(e)
    }
    head.appendChild(el)
    el.src = script.src
  })
}
