
import FooterBar from './components/FooterBar.vue'
import NavBar from './components/Navbar/Navbar.vue'
// import { mapGetters } from 'vuex'
import { Options, Vue } from 'vue-class-component'
@Options({
  data () {
    return {
      keepAliveIncludeRouters: []
    }
  },
  computed: {
    // ...mapGetters(['fullScreen'])
  },
  components: {
    NavBar,
    FooterBar
  },
  created () {
    this.updateKeepAliveInclude()
    // this.keepAliveIncludeRouters = this.$router.options.routes.filter(route => route.meta.keepAlive).map(route => route.name)
  },
  methods: {
    updateKeepAliveInclude () :void {
      this.keepAliveIncludeRouters = this.deepGetRouterKeepAlive(this.$router.options.routes)
    },
    deepGetRouterKeepAlive (routes:any[]): string[] { // 
      let names:string[] = []
      routes.forEach((route:any) => {
        if (route.children) {
          names = names.concat(this.deepGetRouterKeepAlive(route.children))
        } else if (route.meta && route.meta.keepAlive) {
          names.push(route.name)
        }
      })
      return names
    }
  }
})
export default class Layout extends Vue {}
